<template>
    <div class="box">
        <div class="bg"></div>
            <div id="resume"
        @mousewheel="mouse"
        :style="{
        //backgroundImage:`url(${img})`,
        height: `${clientHeight}`
        }">
            <transition
            :duration="{ enter: 2500, leave: 1500 }"
            :leave-active-class="'animate__animated ' + pageleave"
            :enter-active-class="'animate__animated ' + pageenter">
                <div class="section" :style="{height: `${clientHeight}`}" v-if="init == 1">
                    <div class="sec">
                    <Page1></Page1>
                    </div>
                </div>
            </transition>
            <transition
            :duration="{ enter: 2500, leave: 1500 }"
            :leave-active-class="'animate__animated ' + pageleave"
            :enter-active-class="'animate__animated ' + pageenter">
                <div class="section" :style="{height: `${clientHeight}`}" v-if="init == 2">
                    <div class="sec">
                    <Page2></Page2>
                    </div>
                </div>
            </transition>
            <transition
            :duration="{ enter: 1500, leave: 1500 }"
            :leave-active-class="'animate__animated ' + pageleave"
            :enter-active-class="'animate__animated ' + pageenter">
                <div class="section" :style="{height: `${clientHeight}`}" v-if="init == 3">
                    <div class="sec">
                    <Page3></Page3>
                    </div>
                </div>
            </transition>
            <transition
            :duration="{ enter: 1500, leave: 1500 }"
            :leave-active-class="'animate__animated ' + pageleave"
            :enter-active-class="'animate__animated ' + pageenter">
                <div class="section" :style="{height: `${clientHeight}`}" v-if="init == 4">
                    <div class="sec">
                    <Page4></Page4>
                    </div>
                </div>
            </transition>
            <!-- 下方的下一页按钮 -->
            <div class="next">
                <span @click="nextPage">
                    <i class="iconfont icon-arrow-down"></i>
                </span>
            </div>
            <!-- 侧边栏 -->
            <aside>
                <ul>
                    <li v-for="aside in asideData" :key="aside.index" 
                    @click="changeIndex(aside.index)"
                    :class="[aside.index == init ? 'color1' : '']">
                        {{aside.text}}
                    </li>
                </ul>
            </aside>
    </div>
    </div>
</template>

<script>
import Page1 from './Page1.vue'
import Page2 from './Page2.vue'
import Page3 from './Page3.vue'
import Page4 from './Page4.vue'

export default {
    name:'resume',
    components:{
        Page1,
        Page2,
        Page3,
        Page4,

    },
    data () {
        return {
            clientHeight: '100%',
            init: 1,
            lastscroll: 0,
            pageenter:'animate__zoomInDown',
            pageleave:'animate__zoomOutUp',
            timer : null,
            nextFlag:true,
            indexTimer:null,
            indexFlag:true,
            distance:35,
            asideData:[
                {text:1,index:1},
                {text:2,index:2},
                {text:3,index:3},
                {text:4,index:4},
            ]
        }
    },
    methods:{
        mouse (event) {
        // 防止用户短时间内滚动多次，设置滚动间隔大于一秒才能生效
        // 判断滚动间隔时间
            const scrollduration = event.timeStamp - this.lastscroll
            if (scrollduration > 1000 && Math.abs(event.deltaY) >= this.distance) {
                // 将这一次的滚动时间记录为上一次合法的滚动时间
                this.lastscroll = event.timeStamp
                // 判断滚动方向进行操作
                if (event.deltaY > 0) {
                        //向下
                    if (this.init === 4) {
                        this.init = 1
                    } else {
                        this.init = this.init + 1
                    }
                } else {
                    //向上
                    if (this.init === 1) {
                        this.init = 4
                    } else {
                        this.init = this.init - 1
                    }
                }
            }
        },
        nextPage () {
            if(this.nextFlag){
                clearTimeout(this.timer)
                this.nextFlag = false
                if (this.init === 4) {
                        this.init = 1
                    } else {
                        this.init = this.init + 1
                    }
                this.timer = setTimeout(()=>{
                    this.nextFlag = true
                },1500)   
            }
            
        },
        changeIndex(index){
            if(this.indexFlag){
                clearTimeout(this.indexTimer)
                this.indexFlag = false
                this.init = index
                setTimeout(()=>{
                    this.indexFlag = true
                },1000)
            }
        }
    },
    mounted () {
        this.clientHeight = `${document.documentElement.clientHeight}px`
        // 然后监听window的resize事件．在浏览器窗口变化时再设置下背景图高度
        window.onresize = () => {
            this.clientHeight = `${document.documentElement.clientHeight}px`
        }
    }
}
</script>

<style lang="less" scoped>
    .box{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
    .bg{
        width: 100%;
        height: 100%;
        background: url('../assets/static/timg.jpg') no-repeat 0/cover fixed;
        position: relative;
        opacity: .95;
    }
    .bg::after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        /* 从父元素继承 background 属性的设置 */
        background: inherit;
        -webkit-filter: blur(10px);
        -moz-filter: blur(10px);
        -o-filter: blur(10px);
        -ms-filter: blur(10px);
        filter: blur(10px);
    }
    #resume {
    //background-image: url('../assets/static/timg.jpg');
    background-size: cover;
    width: 100%;
    height: 100%;
    background-repeat:no-repeat;
    background-position: center center;
    background-size:100% 100%;
    -moz-background-size:100% 100%;
    background-color: pink;
    //opacity: .7;
        .section {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
        }
    };
    .next {
        width: 100%;
        height: 50px;
        position: fixed;
        bottom: 0;
        font-size: 20px;
        text-align: center;
        line-height: 50px;
        opacity: .8;
        span{
            text-align: center;
            display: inline-block;
            width: 5%;
            height: 100%;
            background: rgba(39, 35, 35, 0.788);
            cursor: pointer;
            user-select: none;
            color: rgba(217, 219, 228, 0.8);
            border-radius: 50%;
        }
    }
    i{
        font-size: 1.75rem;
    }
    aside{
        position: fixed;
        right: 0;
        top: 50%;
        right: 5px;
        transform: translateY(-50%);
        li{
            position: relative;
            width: 55px;
            height: 55px;
            border: #cacaca 1px solid;
            background-color: #F4F4F4;
            text-align: center;
            font-size: 13px;
            line-height: 55px;
            margin-top: -1px;
        }
        li:first-child{
            border-top-right-radius: 15px;
            border-top-left-radius: 15px;
            margin-top: 0px;
        }
        li:last-child{
            border-bottom-left-radius: 15px;
            border-bottom-right-radius: 15px;
        }
        li:hover{
            background: linear-gradient(to bottom, #f0a4b1, #eb3c3c);
            color: white;
            cursor: pointer;
            user-select: none;
        }
    }
    .color1{
        background: linear-gradient(to bottom, #f0a4b1, #eb3c3c);
        color: white;
    }
</style>