import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './assets/css/base.css'
import animate from 'animate.css'
import './assets/font/iconfont.css'

Vue.use(animate)

Vue.config.productionTip = false

new Vue({
    router,
    render: h => h(App)
}).$mount('#app')