<template>
    <div class="box">
        <h1>项目合集</h1>
        <div class="page">
        <transition
        :duration="{ enter: 2500, leave: 1500 }"
        enter-active-class="animate__animated animate__fadeInUp">
            <div class="cont"  v-if="show">
                <div class="radio-wrap">  
                    <div class="radio-group" >   
                        <span 
                        v-for="(tab ,index) in tabs" :key="index" 
                        @click="iscur=index,tabChange('select' + (index + 1))"
                        class="nav" :class="{nav_choose:iscur == tab.count}">
                            {{tab.name}}
                        </span>  
                    </div>  
                    <div style="margin:10px 0;"></div>  
                    <keep-alive>   
                        <component v-bind:is="tabView"></component>  
                    </keep-alive>   
                    </div>  

            </div>
        </transition>
    </div>
    </div>
</template>

<script>
import Select1 from './Select/Select1.vue'
import Select2 from './Select/Select2.vue'
import Select3 from './Select/Select3.vue'

export default {
    data () {
        return {
            show: false,
            tabView: 'select1',
            iscur: 0,   
            tabs: [{name: "Vue简历",count:0}, {name: "网站开发",count:1} ,{name: "Python项目",count:2}]
        }
    },
    components:{
        Select1:Select1,
        Select2:Select2,
        Select3:Select3,
    },
    methods: {
        timeout () {
            setTimeout(() => {
            this.show = true
            }, 1000)
        },
        tabChange:function(tab){  
            this.tabView = tab  
        } 
    },
    mounted () {
        this.timeout()
    }
}
</script>

<style lang="less" scoped>
    .page{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 900px;
        height: 600px;
        margin: auto;
        background: rgba(255, 255, 255, .3);
        box-shadow: 4px 4px 6px 3px rgba(0, 0, 0, .3);
        border-radius: 15px;
    }
    .radio-group{
        display: flex;
        justify-content: center;
    }
    .nav{
        display: flex;
        width: 200px;
        height: 50px;
        border-radius: 20px;
        margin-right: 50px;
        justify-content: center;
        line-height: 50px;
        .nav:last-child{
            margin-right: 0;
        }
    }
    .nav:hover{         
        cursor: pointer;
        user-select: none;
        background-color: rgba(165, 159, 159, 0.308);            
    }
    .nav_choose{
        background: linear-gradient(to right,#fe6785,#fca6aa);
    }
    h1{
        text-align: center;
        padding-top: 37px;
        font-size: 30px;
        color:#a2eee1;
        user-select: none;  
        text-shadow:0px 1px 0px #c0c0c0,    
            0px 2px 0px #b0b0b0,    
            0px 3px 0px #a0a0a0,    
            0px 4px 0px #909090,    
            0px 5px 10px rgba(0, 0, 0, .9);
    }
</style>